import React from "react";
import { useState } from "react";

function Square({ value, OnSquareClick }) {
  return (
    <button className="square" onClick={OnSquareClick}>
      {value}
    </button>
  );
}

function Board({ xIsNext, squares, onPlay }) {
  function handleClick(i) {
    if (calculateWinner(squares) || squares[i]) return;
    const nextSquares = squares.slice();
    if (xIsNext) nextSquares[i] = "X";
    else nextSquares[i] = "O";
    onPlay(nextSquares);
  }

  //displaying the status
  const winner = calculateWinner(squares);
  let next = xIsNext ? "X" : "O";
  let status;
  if (winner) {
    status = "WINNER " + winner;
  } else {
    status = "Next Player " + next;
  }

  return (
    <React.Fragment>
      <div className="status">{status}</div>
      <div className="board-row">
        <Square
          value={squares[0]}
          OnSquareClick={() => {
            handleClick(0);
          }}
        />
        <Square
          value={squares[1]}
          OnSquareClick={() => {
            handleClick(1);
          }}
        />
        <Square
          value={squares[2]}
          OnSquareClick={() => {
            handleClick(2);
          }}
        />
      </div>
      <div className="board-row">
        <Square
          value={squares[3]}
          OnSquareClick={() => {
            handleClick(3);
          }}
        />
        <Square
          value={squares[4]}
          OnSquareClick={() => {
            handleClick(4);
          }}
        />
        <Square
          value={squares[5]}
          OnSquareClick={() => {
            handleClick(5);
          }}
        />
      </div>
      <div className="board-row">
        <Square
          value={squares[6]}
          OnSquareClick={() => {
            handleClick(6);
          }}
        />
        <Square
          value={squares[7]}
          OnSquareClick={() => {
            handleClick(7);
          }}
        />
        <Square
          value={squares[8]}
          OnSquareClick={() => {
            handleClick(8);
          }}
        />
      </div>
    </React.Fragment>
  );
}

export default function Game() {
  const [history, setHistory] = useState([Array(9).fill(null)]);
  const [currMove,setCurrMove] = useState(0);
  const xIsNext = currMove % 2 === 0;

  const currSquare = history[currMove];

  function handlePlay(nextSquares) {
    const nextHistory = [...history.slice(0,currMove+1),nextSquares];
    setHistory(nextHistory);
    setCurrMove(currMove+1);
  }

  function jumpTo(nextMove){
    setCurrMove(nextMove);
  }

  const moves = history.map(
    (squares,move) =>{
      let description;
      if(move > 0){
        description = "Go to move #" + move;
      }else{
        description = "Go to Start";
      }
      return(
        <li key={move}>
          <button onClick={() => jumpTo(move)}>{description}</button>
        </li>
      );
    }
  );

  return (
    <div className="game">
      <div className="game-board">
        <Board xIsNext={xIsNext} squares={currSquare} onPlay={handlePlay} />
      </div>
      <div className="game-info">
        <ol>{moves}</ol>
      </div>
    </div>
  );
}

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6]
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}



